/* eslint-disable @typescript-eslint/no-unsafe-argument */
import cors from '@fastify/cors';
import { fastifyStatic } from '@fastify/static';
import Fastify from 'fastify';
import { extractRelations, queryEngineForSetup, Setup } from 'studio';

export const prepareServer = async (setup: Setup) => {
	const app = Fastify();
	app.setErrorHandler((err, _, reply) => {
		console.error(err);
		reply.status(500).send({ error: err.message });
	});

	const jsonStringify = (data: any) => JSON.stringify(data, (key, value) => (typeof value === 'bigint' ? value.toString() : value));

	const { query, delete: del, insert, update, tables, tablesConfig } = await queryEngineForSetup(setup);
	const relations = extractRelations(tablesConfig);

	app.get('/tables', async () => {
		const res = await tables();
		return { tables: res, relations };
	});

	app.post('/select', async (req, reply) => {
		const { table, columns, where, orderBy, limit, offset } = (await req.body) as any;

		try {
			const result = await query({
				table,
				columns,
				where,
				orderBy,
				limit,
				offset,
			});
			return jsonStringify(result);
		} catch (e: any) {
			console.error('error', e);
			reply.code(500);
			return { error: e.message };
		}
	});

	app.post('/delete', async (req) => {
		const { table, where } = (await req.body) as any;
		await del({ table, where });
		return { status: 'ok' };
	});

	app.post('/insert', async (req) => {
		const { table, data } = (await req.body) as any;
		await insert({ table, data });
		return { status: 'ok' };
	});

	app.post('/update', async (req) => {
		const { table, where, data } = (await req.body) as any;
		await update({ table, where, data });
		return { status: 'ok' };
	});

	app.register(fastifyStatic, {
		root: __dirname,
	});

	app.register(cors);

	return {
		start: (params: { host: string; port: number; cb: (err: Error | null, address: string) => void }) => {
			app.listen({ host: params.host, port: params.port }, params.cb);
		},
	};
};
