function assertSQLiteTable(value: unknown): asserts value is AnySQLiteTable {
	if (!is(value, SQLiteTable<any>)) {
		throw new Error();
	}
}

function assertPgTable(value: unknown): asserts value is AnyPgTable {
	if (!is(value, PgTable<any>)) {
		throw new Error();
	}
}

function assertMysqlTable(value: unknown): asserts value is AnyMySqlTable {
	if (!is(value, MySqlTable<any>)) {
		throw new Error();
	}
}

export type FilterOp = [string, string, any];

export type Filter = {
	and?: Filter[];
	or?: Filter[];
	op?: FilterOp;
};

export function filtersToSQL(filters: Filter): SQL | undefined {
	return Object.entries(filters).reduce<SQL | undefined>(
		(acc, [key, value]) => {
			if (key === 'and') {
				return and(acc, ...value.map((v) => filtersToSQL(v)));
			}

			if (key === 'or') {
				return or(acc, ...value.map((v) => filtersToSQL(v)));
			}

			if (key === 'op') {
				const op = value as FilterOp;
				return sql.fromList([
					sql.identifier(op[0]),
					sql` `,
					sql.raw(op[1]),
					sql` `,
					op[2],
				]);
			}

			throw new Error(`Unknown filter key: ${key}`);
		},
		undefined
	);
}

export type Setup =
	| {
			type: 'sqlite';
			db: BaseSQLiteDatabase<'async' | 'sync', any, Record<string, never>>;
			schema: Record<string, AnySQLiteTable | Relations>;
	  }
	| {
			type: 'mysql';
			db: MySqlDatabase<any, any>;
			schema: Record<string, AnyMySqlTable | Relations>;
	  }
	| {
			type: 'pg';
			db: PgDatabase<any, any>;
			schema: Record<string, AnyPgTable | Relations>;
	  };

const columnMapper = (column: Column) => {
	if (is(column, SQLiteTimestamp)) {
		return (input: string) => {
			if (input === undefined || input === null) return null;
			return new Date(input);
		};
	}

	if (is(column, PgTimestamp) || is(column, PgDate)) {
		return (input: string) => {
			if (input === undefined || input === null) return null;
			return new Date(input);
		};
	}

	if (is(column, MySqlTimestamp) || is(column, MySqlDate)) {
		return (input: string) => {
			if (input === undefined || input === null) return null;
			return new Date(input);
		};
	}

	if (
		is(column, PgInteger) ||
		is(column, PgSmallInt) ||
		is(column, PgSerial) ||
		is(column, PgSmallSerial) ||
		is(column, PgDoublePrecision) ||
		is(column, PgReal) ||
		is(column, SQLiteInteger) ||
		is(column, MySqlInt) ||
		is(column, MySqlTinyInt) ||
		is(column, MySqlSmallInt) ||
		is(column, MySqlMediumInt) ||
		is(column, MySqlFloat) ||
		is(column, MySqlDouble)
	) {
		return (input: string) => {
			if (input === undefined || input === null) return null;
			return Number(input);
		};
	}

	if (is(column, PgBigInt53) || is(column, PgBigSerial53)) {
		return (input: string) => Number(input);
	}

	if (is(column, PgBigInt64) || is(column, PgBigSerial64)) {
		return (input: string) => BigInt(input);
	}

	if (is(column, MySqlBigInt53)) {
		return (input: string) => Number(input);
	}

	if (is(column, MySqlBigInt64)) {
		return (input: string) => BigInt(input);
	}

	return (input: string) => input;
};

const queryForSqliteAsync =
	(
		db: BaseSQLiteDatabase<'async', any, Record<string, never>>,
		tablesMap: Record<string, AnySQLiteTable>
	) =>
	(params: {
		table: string;
		columns: string[] | undefined;
		where: Filter | undefined;
		orderBy:
			| {
					column: string;
					type: 'ASC' | 'DESC';
			  }
			| undefined;
		limit: number;
		offset: number;
	}) => {
		const table = tablesMap[params.table];
		const config = sqliteTableConfig(table);

		const hasPartial = (params.columns && params.columns.length > 0) ?? false;
		const columns: Record<string, AnySQLiteColumn> = (
			config.columns || []
		).reduce((acc, it) => {
			acc[it.name] = it;
			return acc;
		}, {} as Record<string, AnySQLiteColumn>);

		const partialSelect: Record<string, AnySQLiteColumn> = (
			config.columns || []
		).reduce((acc, it) => {
			if (params.columns?.includes(it.name)) {
				acc[it.name] = it;
			}
			return acc;
		}, {} as Record<string, AnySQLiteColumn>);

		const orderByColumn = params.orderBy
			? columns[params.orderBy.column]
			: undefined;
		const orderByExpr = orderByColumn
			? params.orderBy!.type === 'ASC'
				? [asc(orderByColumn)]
				: [desc(orderByColumn)]
			: ([] as AnySQLiteColumn[]);

		const q = hasPartial ? db.select(partialSelect) : db.select();
		return q
			.from(table)
			.where(params.where ? filtersToSQL(params.where) : undefined)
			.orderBy(...orderByExpr)
			.limit(params.limit)
			.offset(params.offset)
			.all();
	};

