const deleteForSqliteAsync =
	(
		db: BaseSQLiteDatabase<'async', any, Record<string, never>>,
		tablesMap: Record<string, AnySQLiteTable>
	) =>
	(params: { table: string; where: Filter }) => {
		const table = tablesMap[params.table]! as AnySQLiteTable;

		return db.delete(table).where(filtersToSQL(params.where)).run();
	};
