const insertForSqliteAsync =
	(
		db: BaseSQLiteDatabase<'async', any, Record<string, never>>,
		tablesMap: Record<string, AnySQLiteTable>,
		tableToColumnNames: Record<
			string,
			Record<
				string,
				{
					name: string;
					mapper: (str: string) => any;
				}
			>
		>
	) =>
	(params: { table: string; data: Record<string, any> }) => {
		const table = tablesMap[params.table];
		const columnNamesMap = tableToColumnNames[params.table];

		const data = convertToTsColumns(params.data, columnNamesMap);
		return db.insert(table).values(data).run();
	};

