const convertToTsColumns = (
	data: Record<string, any>,
	columnNamesMap: Record<
		string,
		{
			name: string;
			mapper: (str: string) => any;
		}
	>
) =>
	Object.fromEntries(
		Object.entries(data).map((it) => {
			const { name: tsName, mapper } = columnNamesMap[it[0]];
			if (!tsName) throw new Error(`No column name '${it[0]}'`);
			return [tsName, mapper(it[1])];
		})
	);

const typeForTable = (column: Column) => {
	if (
		is(column, SQLiteBoolean) ||
		is(column, PgBoolean) ||
		is(column, MySqlBoolean)
	) {
		return 'boolean';
	}

	if (
		is(column, SQLiteInteger) ||
		is(column, PgInteger) ||
		is(column, PgBigInt53) ||
		is(column, PgBigSerial53) ||
		is(column, PgReal) ||
		is(column, PgSerial) ||
		is(column, PgSmallInt) ||
		is(column, PgSmallSerial) ||
		is(column, PgDoublePrecision) ||
		is(column, MySqlInt) ||
		is(column, MySqlTinyInt) ||
		is(column, MySqlSmallInt) ||
		is(column, MySqlMediumInt) ||
		is(column, MySqlFloat) ||
		is(column, MySqlBigInt53) ||
		is(column, MySqlDouble)
	) {
		return 'number';
	}

	if (
		is(column, SQLiteTimestamp) ||
		is(column, PgTimestamp) ||
		is(column, PgTimestampString) ||
		is(column, PgDate) ||
		is(column, PgDateString) ||
		is(column, MySqlTimestamp) ||
		is(column, MySqlTimestampString) ||
		is(column, MySqlDate) ||
		is(column, MySqlDateString)
	) {
		return 'date';
	}

	if (
		is(column, PgArray) ||
		is(column, PgJson) ||
		is(column, PgJsonb) ||
		is(column, MySqlJson) ||
		is(column, SQLiteBlobJson)
	) {
		return 'json';
	}

	return 'string';
};

export const queryEngineForSetup = async (setup: Setup) => {
	if (setup.type === 'sqlite') {
		const tablesMap = Object.values(setup.schema).reduce((acc, it) => {
			if (is(it, SQLiteTable<any>)) {
				const config = sqliteTableConfig(it);
				acc[config.name] = it;
			}
			return acc;
		}, {} as Record<string, AnySQLiteTable>);

		const tableToColumnNames = Object.values(setup.schema).reduce(
			(acc, it) => {
				if (is(it, SQLiteTable<any>)) {
					const config = sqliteTableConfig(it);
					const columnNamesMap = Object.fromEntries(
						Object.entries(getTableColumns(it)).map((col) => [
							col[1].name,
							{
								name: col[0],
								mapper: columnMapper(col[1]),
							},
						])
					);
					acc[config.name] = columnNamesMap;
				}
				return acc;
			},
			{} as Record<
				string,
				Record<
					string,
					{
						name: string;
						mapper: (str: string) => any;
					}
				>
			>
		);

		const tables = async () => {
			return Promise.all(
				Object.values(setup.schema)
					.filter((it) => is(it, SQLiteTable<any>))
					.map(async (it) => {
						assertSQLiteTable(it);

						const config = sqliteTableConfig(it);

						const primaryKeys = config.primaryKeys
							.map((it) => it.columns.map((column) => column.name))
							.concat(config.columns.map((it) => (it.primary ? it.name : '')))
							.filter(Boolean)
							.flat();

						const tableName = config.name;
						const columns = config.columns.map((it) => {
							return {
								name: it.name,
								type: typeForTable(it),
								_type: it.getSQLType(),
							};
						});
						const table = tablesMap[tableName];

						const result = await setup.db
							.select({
								count: sql<number>`count(*)`,
							})
							.from(table)
							.all();

						return {
							name: tableName,
							columns,
							count: result[0]!.count,
							primaryKeys,
						};
					})
			);
		};

		const { db } = setup;

		const tablesConfig = extractTablesRelationalConfig(
			setup.schema,
			createTableRelationsHelpers
		);
		const isSync = is(
			db,
			BaseSQLiteDatabase<'sync', any, Record<string, never>>
		);
		if (isSync) {
			return {
				query: queryForSqliteSync(db, tablesMap),
				delete: deleteForSqliteSync(db, tablesMap),
				insert: insertForSqliteSync(db, tablesMap, tableToColumnNames),
				update: updateForSqliteSync(db, tablesMap, tableToColumnNames),
				tables,
				tablesConfig,
			};
		}
		const isAsync = is(
			db,
			BaseSQLiteDatabase<'async', any, Record<string, never>>
		);
		if (isAsync) {
			return {
				query: queryForSqliteAsync(db, tablesMap),
				delete: deleteForSqliteAsync(db, tablesMap),
				insert: insertForSqliteAsync(db, tablesMap, tableToColumnNames),
				update: updateForSqliteAsync(db, tablesMap, tableToColumnNames),
				tables,
				tablesConfig,
			};
		}

		throw new Error('never');
	}

	if (setup.type === 'pg') {
		const tablesMap = Object.values(setup.schema).reduce((acc, it) => {
			if (is(it, PgTable<any>)) {
				const config = pgTableConfig(it);
				acc[config.name] = it;
			}
			return acc;
		}, {} as Record<string, AnyPgTable>);

		const tableToColumnNames = Object.values(setup.schema).reduce(
			(acc, it) => {
				if (is(it, PgTable<any>)) {
					const config = pgTableConfig(it);
					const columnNamesMap = Object.fromEntries(
						Object.entries(getTableColumns(it)).map((col) => [
							col[1].name,
							{
								name: col[0],
								mapper: columnMapper(col[1]),
							},
						])
					);
					acc[config.name] = columnNamesMap;
				}
				return acc;
			},
			{} as Record<
				string,
				Record<
					string,
					{
						name: string;
						mapper: (str: string) => any;
					}
				>
			>
		);

		const tables = async () => {
			return Promise.all(
				Object.values(setup.schema)
					.filter((it) => is(it, PgTable<any>))
					.map(async (it) => {
						assertPgTable(it);

						const config = pgTableConfig(it);

						const primaryKeys = config.primaryKeys
							.map((it) => it.columns.map((column) => column.name))
							.concat(config.columns.map((it) => (it.primary ? it.name : '')))
							.filter(Boolean)
							.flat();

						const tableName = config.name;
						const columns = config.columns.map((it) => ({
							name: it.name,
							type: typeForTable(it),
							_type: it.getSQLType(),
						}));

						const table = tablesMap[tableName]!;

						const result = await setup.db
							.select({
								count: sql<number>`count(*)`.mapWith((it) => Number(it)),
							})
							.from(table)
							.execute();

						return {
							name: tableName,
							columns,
							count: result[0]!.count,
							primaryKeys,
						};
					})
			);
		};

		const tablesConfig = extractTablesRelationalConfig(
			setup.schema,
			createTableRelationsHelpers
		);

		return {
			query: queryForPg(setup.db, setup.schema),
			delete: deleteForPg(setup.db, tablesMap),
			insert: insertForPg(setup.db, tablesMap, tableToColumnNames),
			update: updateForPg(setup.db, tablesMap, tableToColumnNames),
			tables,
			tablesConfig,
		};
	}

	if (setup.type === 'mysql') {
		const tablesMap = Object.values(setup.schema).reduce((acc, it) => {
			if (is(it, MySqlTable<any>)) {
				const config = mysqlTableConfig(it);
				acc[config.name] = it;
			}
			return acc;
		}, {} as Record<string, AnyMySqlTable>);

		const tableToColumnNames = Object.values(setup.schema).reduce(
			(acc, it) => {
				if (is(it, MySqlTable<any>)) {
					const config = mysqlTableConfig(it);
					const columnNamesMap = Object.fromEntries(
						Object.entries(getTableColumns(it)).map((col) => [
							col[1].name,
							{
								name: col[0],
								mapper: columnMapper(col[1]),
							},
						])
					);
					acc[config.name] = columnNamesMap;
				}
				return acc;
			},
			{} as Record<
				string,
				Record<
					string,
					{
						name: string;
						mapper: (str: string) => any;
					}
				>
			>
		);

		const tables = async () => {
			return Promise.all(
				Object.values(setup.schema)
					.filter((it) => is(it, MySqlTable<any>))
					.map(async (it) => {
						assertMysqlTable(it);

						const config = mysqlTableConfig(it);

						const primaryKeys = config.primaryKeys
							.map((it) => it.columns.map((column) => column.name))
							.concat(config.columns.map((it) => (it.primary ? it.name : '')))
							.filter(Boolean)
							.flat();

						const tableName = config.name;

						const columns = config.columns.map((it) => ({
							name: it.name,
							type: typeForTable(it),
							_type: it.getSQLType(),
						}));

						const table = tablesMap[tableName]!;

						const result = await setup.db
							.select({
								count: sql<number>`count(*)`,
							})
							.from(table)
							.execute();

						return {
							name: tableName,
							columns,
							count: result[0]!.count,
							primaryKeys,
						};
					})
			);
		};

		const tablesConfig = extractTablesRelationalConfig(
			setup.schema,
			createTableRelationsHelpers
		);

		return {
			query: queryForMysql(setup.db, tablesMap),
			delete: deleteForMysql(setup.db, tablesMap),
			insert: insertForMysql(setup.db, tablesMap, tableToColumnNames),
			update: updateForMysql(setup.db, tablesMap, tableToColumnNames),
			tables,
			tablesConfig,
		};
	}
	throw new Error('unknown setup');
};

export const extractRelations = (tablesConfig: {
	tables: TablesRelationalConfig;
	tableNamesMap: Record<string, string>;
}) => {
	const rels = Object.values(tablesConfig.tables)
		.map((it) =>
			Object.entries(it.relations).map((it1) => {
				const normalized = normalizeRelation(
					tablesConfig.tables,
					tablesConfig.tableNamesMap,
					it1[1]
				);
				const rel = it1[1];
				const refTable = rel.referencedTableName;
				const fields = normalized.fields.map((it) => it.name).flat();
				const refColumns = normalized.references.map((it) => it.name).flat();
				return {
					name: it1[0],
					table: it.dbName,
					colmns: fields,
					refTable: refTable,
					refColumns: refColumns,
				};
			})
		)
		.flat();
	return rels;
};
